import React from "react";
import Box from "@mui/material/Box";
import { PixyButton } from "../../components/pixyComponents";
import landingSectionAImg from "../../assets/images/back.png";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../../components/pixyComponents/typography/TypographyTitle";
import TypographySubtitle, { TYPOGRAPHY_SUBTITLE_KIND } from "../../components/pixyComponents/typography/TypographySubtitle";
import { Grid } from "@mui/material";

export const LandingSectionA = () => {
  return (
    <Box
      component="section"
      sx={{
        minHeight: "600px",
        display: "flex",
        alignItems: "center",
        height: "calc(100vH - 340px)",
        backgroundImage: `linear-gradient(90deg, rgba(0, 0, 0, 0.7)28.58%,rgba(0, 0, 0, 0) 59.69%),url(${landingSectionAImg})`,
        backgroundBlendMode: "darken",
        backgroundSize: "cover",
        backgroundPosition: "center center",
        paddingLeft: "12%",        
        ['@media(min-width: 1650px)'] : {
          height: "900px"
        }
      }}
    >
      <Grid container spacing="3">
        <Grid item xs={12}>
          <Box>
            <TypographySubtitle kind={TYPOGRAPHY_SUBTITLE_KIND.Uppercase} sx={{
              ['@media(min-width: 1660px)'] : {
                fontSize: "28px",
              }
            }}>
              Crowdfunding Reimagined
            </TypographySubtitle>
          </Box>
        </Grid>
        <Grid item xs={12} sx={{
          ['@media(min-width: 780px)'] : {
            marginTop: 4
          }
        }}>
          <Box>
            <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Primary} sx={{
                ['@media(min-width: 1660px)'] : {
                  fontSize: "54px",
                }
              }}>
              MINT principal protected NFTs<br></br>
              that fund the projects you love
            </TypographyTitle>
          </Box>
        </Grid>
        <Grid item xs={12} sx={{
          ['@media(min-width: 780px)'] : {
            marginTop: 4
          },
        }}>
          <Box>
            <Grid container spacing="6">
              <Grid item xs={12} sm={2}>
                <PixyButton upperCase={false}
                sx={{
                  ['@media(min-width: 1660px)'] : {
                    fontSize: "24px",
                  }
                }}
                onClick={() => {
                  document.getElementById("catalog")!.scrollIntoView({
                    behavior: "smooth"
                  });
                }}>Shop NFTs</PixyButton>
              </Grid>
              <Grid item xs={12} sm={2} sx={{
                ['@media(min-width: 780px)'] : {
                  marginLeft: -7
                },
                ['@media(min-width: 1680px)'] : {
                  marginLeft: -10
                },
                ['@media(min-width: 1800px)'] : {
                  marginLeft: -12
                },
                ['@media(min-width: 1921px)'] : {
                  marginLeft: -14
                }
              }}>
                <PixyButton
                  sx={{
                    ['@media(min-width: 1660px)'] : {
                      fontSize: "24px",
                    }
                  }}
                  onClick={() => {
                    document.getElementById("how-it-works")!.scrollIntoView({
                      behavior: "smooth"
                    });
                  }}
                  upperCase={false}
                  variant="outlined"
                >
                  How It Works
                </PixyButton>
              </Grid>
            </Grid>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};
