import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import { PixyNFTCard } from "../../components/pixyComponents";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../../components/pixyComponents/typography/TypographyTitle";
import { MockNFTDatastore } from "../../MockNFTDatastore";
import { PageSection } from "../../containers/PageSection";

export const Catalog = () => {
  return (
    <PageSection id="catalog" sx={{
      ['@media(max-width: 780px)'] : {
        padding: '0px 10px'
      },
    }}>
      <Box sx={{ my: 4, textAlign: "center", marginBottom: "60px",  marginTop: "70px"}}>
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary}>
          Featured NFTs
        </TypographyTitle>
      </Box>
      <Box sx={{ 
          my: 4, 
          marginLeft: "12%", 
          marginBottom: "60px", 
          marginTop: "80px",
          ['@media(max-width: 780px)'] : {
            marginLeft: 0
          },
        }}>
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary} sx={{
          ['@media(max-width: 780px)'] : {
            lineHeight: "30px",
            textAlign: "center" 
          },
        }}>
          Mint Original DanKu Zone NFTs
        </TypographyTitle>
        <Box sx={{
          fontSize: 24,
          color: "#d7d8d9",
          marginTop: "35px",
          ['@media(max-width: 780px)'] : {
            fontSize: 16
          },
        }}>
          Each NFT provides a viewing key to Danku Zone's new Youtube servies, as well<br></br>
          as access to exclusive channels on Dnaku's discord that are full of alpha.
        </Box>
      </Box>
      <div style={{display: "flex", justifyContent: "center"}}>
        <div>
          <Grid container spacing="18">
            {Object.keys(MockNFTDatastore).map((key: string) => {
              const { image, ...rest } = MockNFTDatastore[key];
              return <Grid item xs={12} md={4}><PixyNFTCard key={image} image={image} {...rest} /></Grid>;
            })}
          </Grid>
        </div>
      </div>
      <Box sx={{ my: 4, textAlign: "center", margin: "60px 0px", fontSize : "18px", color: "#c1bdbd" }}>
        By minting NFTs you gain Private key to access exclusive content, project members-only<br></br>
        discord channels and other rewards for project supporters.
      </Box>
      <Box sx={{ my: 4, textAlign: "center", margin: "0px 0px 60px 0px", fontSize : "18px", color: "#c1bdbd" }}>
        At the end of the project each NFT is redeemable for its face value. All NFTs are instantly tradeable and transferable on the secondary market.
      </Box>
    </PageSection>
  );
};
